  font-family: DM Sans, sans-serif;
  transition: background-color .8s;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

a, button {
  cursor: pointer;
}

@media screen and (max-width: 375px) {
