@media screen and (min-width: 319px) and (max-width: 769px) {
  .header-menu-mob {
    stroke: #111;
    width: 28px;
    height: 28px;
    background-color: #fff;
    transition: all .3s;
  }

  .header-menu-mob:hover, .header-menu-mob:focus {
    stroke: #4f2ee8;
  }

  .menu-burger-open {
    cursor: pointer;
    background-color: #111;
    border: none;
    border-radius: 50%;
    outline: none;
    justify-content: center;
    align-items: flex-end;
    margin-left: 14px;
    padding: 0;
    transition: all .3s;
    display: flex;
  }

  .menu-burger-open .header-menu-mob {
    opacity: 1;
    transition: opacity .3s;
  }

  .menu-burger-open .header-menu-mob-close {
    background-color: #fff;
    transition: opacity .3s;
  }

  .mobile-menu-container {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    width: 100%;
    height: 90vh;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0);
    background-image: url("background-mobile-burger.fdafd70b.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: contain;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
    flex-direction: column;
    transition: all .3s;
    display: flex;
    position: fixed;
    top: 58px;
    left: 0;
    transform: translateX(100%);
  }

  .mobile-menu-container.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: visible;
    transform: translate(0);
  }

  .mobile-menu-close-button {
    cursor: pointer;
    width: 24px;
    height: 24px;
    background-color: #111;
    border: none;
    border-radius: 50%;
    outline: none;
    justify-content: center;
    align-items: center;
    margin: 18px;
    padding: 0;
    animation: all .3s;
    display: flex;
  }

  .header-menu-mob-close.burger-toggle-hidden {
    transition: opacity .3s ease-out, transform .3s ease-out;
    display: none;
    transform: scale(0);
  }

  .header-menu-mob.burger-toggle-hidden {
    transition: opacity .3s ease-out, height .3s ease-out;
    display: none;
  }

  .header-menu-mob-close {
    stroke: #111;
    transition: all .3s;
    display: flex;
  }

  .header-menu-mob-close:hover, .header-menu-mob-close:focus {
    stroke: #4f2ee8;
  }

  .user-info-bar {
    height: 37px;
    align-items: center;
    gap: 9px;
    margin-top: 20px;
    margin-left: 20px;
    display: inline-flex;
  }

  .user-info-bar > .user-name {
    color: #fff;
    font-family: DM Sans;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: 1.31;
  }

  .nav-mob-menu, .nav-list-mob-menu {
    margin: 0 auto;
    display: flex;
  }

  .nav .nav-active {
    background-color: #eac645;
    border-radius: 36px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    transition: all .3s;
    display: flex;
  }

  .nav-link {
    color: #fff;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
  }

  .nav-list {
    flex-direction: column;
    justify-content: center;
    align-items: start;
    gap: 8px;
    margin-top: 15.5vh;
    display: flex;
  }

  .nav-list .nav-active {
    background-color: #eac645;
    border-radius: 36px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    transition: all .3s;
    display: flex;
  }

  .nav-item {
    z-index: 10;
    padding-top: 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28;
    transition: all .3s;
    position: relative;
  }

  .btn-header-mob-logout {
    max-width: fit-content;
    text-transform: none;
    letter-spacing: -.04em;
    color: #111;
    background: #fff;
    border: 1.5px solid #111;
    border-radius: 18px;
    gap: 4px;
    padding: 13px 17px;
    font-size: 14px;
    line-height: 1.28;
    transition: all .3s;
    display: flex;
    position: fixed;
    bottom: 20px;
    left: 20px;
  }

  .btn-header-mob-logout:hover, .btn-header-mob-logout:focus {
    color: #fff;
    background: #4f2ee8;
  }

  .mobile-inactive-link {
    color: #fff;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
  }
}

@media screen and (min-width: 769px) and (max-width: 1440px) {
  .menu-burger-open, .header-menu-mob, .mobile-menu-close-button, .mobile-menu-burger, .header-menu-mob-close, .mobile-menu-container {
