  cursor: not-allowed;
  pointer-events: none;
  background-color: rgba(31, 26, 44, .635);
  justify-content: center;
  align-items: center;
  transition: background-color .8s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

