  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0);
  border: 4px solid rgba(0, 0, 0, 0);
  border-left-color: #eac645;
  border-radius: 50%;
  animation: rotateX 1.5s linear infinite;
  display: block;
  position: relative;
}

.loader:before {
  content: "";
  background-color: rgba(0, 0, 0, 0);
  border: 4px solid rgba(0, 0, 0, 0);
  border-right-color: #eac645;
  border-radius: 50%;
  animation: rotateX .7s linear infinite reverse;
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
}

@keyframes rotateX {
  from {
    transform: rotateZ(0deg);
  }

  to {
    transform: rotateZ(360deg);
  }
}

